<template>
  <div>
    <v-row>
      <v-col class="text-center">
        <img alt="Vue big-image" src="../assets/flower.jpg" class="big-image" />
        <br />
        <br />
        <br />
        <p class="text-h3">{{ this.$route.params.flowerId }} </p>
        <p>   </p>
        <p> (Reload)   ?</p>
        <p> vue-router   </p>
        <v-pagination v-model="flowerId" :length="8" circle></v-pagination>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkbox: true, //
      sheet: false, //
      flowerId: parseInt(this.$route.params.flowerId), //
    };
  },
  computed: {},
  methods: {},
  watch: {
    flowerId(newValue) {
      this.$router.push({ path: "", params: { flowerId: newValue } });
    },
  },
  created() {
    this.$root.contentTitle = "Flower";
  },
  mounted() {},
  destroyed() {},
};
</script>

<style lang="scss" scoped>
.big-image {
  border-radius: 12px;
  width: 100%;
}
</style>