<script>
/* global axios */

export default {
  data() {
    return {
      devDialog: false,
      settingsDialog: false,
      loginDialog: false,
      signupDialog: false,
      loginForm: {
        email: "",
        pw: "",
      },
      signinForm: {
        email: "",
        nickname: "",
        pw: "",
      },
    };
  },
  computed: {},
  methods: {
    async login() {
      let response = await axios.post("/ajax/login", this.loginForm);
      console.log(response);
    },
    async signup() {
      let response = await axios.post("/ajax/login", this.signinForm);
      console.log(response);
    },
  },
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
};
</script>

